<template>
	<fieldset class="doc">
		<h3>Документ</h3>
		<div class="wrapper">
<!--********** document **********-->
			<div class="item item_document">
				<label class="required" for="document">Тип документа:</label>
				<select id="document" name="document"
					required
					v-model.trim="document"
					@input="setDocument($event.target.value)"
				>
					<option
						v-for="(docum, ind) of documArray"
						v-bind:key="ind"
						v-bind:value="docum"
					> {{docum}}
					</option>
				</select>
				<span class="error-title" v-if="$v.document.$dirty && !$v.document.required">Обязательное поле</span>
				<span class="error" v-if="($v.document.$dirty && !$v.document.required) || !$v.document.required" >&#10094;</span>
				<span class="done" v-if="$v.document.$dirty && $v.document.required">&#10003;</span>
			</div>
<!--********** series **********-->
			<div class="item">
				<label for="series" >Серия:</label>
				<input type="text" id="series" name="series"
					pattern="[A-Za-zА-Яа-яёЁ\.0-9\-, ]{4,}"
					v-model.trim="series"
					@input="setSeries($event.target.value)"
				/>
				<span class="error-title" v-if="$v.series.$dirty && !$v.series.minLength" >Поле должно содержать минимум {{$v.series.$params.minLength.min}} символа</span>
				<span class="error" v-if="$v.series.$dirty && !$v.series.minLength" >&#10094;</span>
				<span class="done" v-if="$v.series.$dirty && $v.series.required && $v.series.minLength" >&#10003;</span>
			</div>
<!--********** numberDoc **********-->
			<div class="item">
				<label for="numberDoc">Номер:</label>
				<input type="text" id="numberDoc" name="numberDoc"
				placeholder="6 цифр"
					pattern="[0-9]{6}"
					v-model.trim="numberDoc"
					@input="setNumberDoc($event.target.value)"
				/>
				<span class="error-title" v-if="($v.numberDoc.$dirty && !$v.numberDoc.minLength ) || !$v.numberDoc.numeric && $v.numberDoc.required || !$v.numberDoc.maxLength">Поле должно содержать {{$v.numberDoc.$params.minLength.min}} цифр</span>
				<span class="error" v-if="($v.numberDoc.$dirty && !$v.numberDoc.minLength ) || (!$v.numberDoc.numeric && $v.numberDoc.required)
				|| !$v.numberDoc.maxLength" >&#10094;</span>
				<span class="done" v-if="$v.numberDoc.$dirty && $v.numberDoc.required && $v.numberDoc.minLength && $v.numberDoc.numeric && $v.numberDoc.maxLength">&#10003;</span>
			</div>
<!--********** whoIssued **********-->
			<div class="item item_whoIssued">
				<label for="whoIssued" >Кем выдан:</label>
				<textarea  id="whoIssued" name="whoIssued"
					pattern="[A-Za-zА-Яа-яёЁ\.0-9-, ]{4,}"
					v-model.trim="whoIssued"
					@input="setWhoIssued($event.target.value)"
				/>
				<span class="error-title" v-if="$v.whoIssued.$dirty && !$v.whoIssued.minLength" >Поле должно содержать минимум {{$v.whoIssued.$params.minLength.min}} букв</span>
				<span class="error" v-if="$v.whoIssued.$dirty && !$v.whoIssued.minLength" >&#10094;</span>
				<span class="done" v-if="$v.whoIssued.$dirty && $v.whoIssued.required && $v.whoIssued.minLength" >&#10003;</span>
			</div>
<!--********** dateIssued **********-->
			<div class="item item_dateIssued">
				<label class="required" for="dateIssued" >Дата выдачи:</label>
				<input type="date" id="dateIssued" name="dateIssued"
					required
					v-model.trim="dateIssued"
					@input="setDateIssued($event.target.value)"
				/>
				<span class="error-title" v-if="$v.dateIssued.$dirty && !$v.dateIssued.required">Обязательное поле</span>
				<span class="error" v-if="($v.dateIssued.$dirty && !$v.dateIssued.required) || !$v.dateIssued.required" >&#10094;</span>
				<span class="done" v-if="$v.dateIssued.$dirty && $v.dateIssued.required ">&#10003;</span>
			</div>
		</div>
	</fieldset>
</template>

<script>
import { required, minLength, maxLength, numeric } from 'vuelidate/lib/validators'

export default {
	data() {
		return {
			document: '',
			series: '',
			numberDoc: '',
			whoIssued: '',
			dateIssued: '',
			documArray: ['Паспорт', 'Свидетельство о рождении', 'Вод.удостоверение']
		}
	},
	validations: {
		document: {
			required,
			minLength: minLength(3),
		},
		series: {
			minLength: minLength(4),
			required
		},
		numberDoc: {
			minLength: minLength(6),
			maxLength: maxLength(6),
			required,
			numeric
		},
		whoIssued: {
			minLength: minLength(7),
			required
		},
		dateIssued: {
			required,
		}
	},
	methods: {
		setDocument(value) {
			this.document = value;
			this.$v.document.$touch();
		},
		setSeries(value) {
			this.series = value;
			this.$v.series.$touch();
		},
		setNumberDoc(value) {
			this.numberDoc = value;
			this.$v.numberDoc.$touch();
		},
		setWhoIssued(value) {
			this.whoIssued = value;
			this.$v.whoIssued.$touch();
		},
		setDateIssued(value) {
			this.dateIssued = value;
			this.$v.dateIssued.$touch();
		},
	}
}
</script>

<style lang="sass" scoped>
.item
	&_document
		select
			height: 36px
	&_dateIssued
		input
			height: 32px
			width: 202px
</style>
