<template>
	<fieldset class="address">
		<h3>Адрес</h3>
		<div class="wrapper">
<!--********** postIndex **********-->
			<div class="item">
				<label for="postIndex">Индекс:</label>
				<input type="text" id="postIndex" name="postIndex"
					pattern="[0-9]{6}"
					v-model.trim="postIndex"
					@input="setPostIndex($event.target.value)"
				/>
				<span class="error-title" v-if="($v.postIndex.$dirty && !$v.postIndex.minLength ) || !$v.postIndex.numeric && $v.postIndex.required">Поле должно содержать минимум {{$v.postIndex.$params.minLength.min}} цифр</span>
				<span class="error" v-if="($v.postIndex.$dirty && !$v.postIndex.minLength ) || !$v.postIndex.numeric && $v.postIndex.required" >&#10094;</span>
				<span class="done" v-if="$v.postIndex.$dirty && $v.postIndex.required && $v.postIndex.minLength && $v.postIndex.numeric">&#10003;</span>
			</div>
<!--********** country **********-->
			<div class="item">
				<label for="country" >Страна:</label>
				<input type="text" id="country" name="country"
					pattern="[А-Яа-яёЁ\.0-9\-, ]{4,}"
					v-model.trim="country"
					@input="setCountry($event.target.value)"
				/>
				<span class="error-title" v-if="$v.country.$dirty && !$v.country.minLength">Поле должно содержать минимум {{$v.country.$params.minLength.min}} буквы</span>
				<span class="error" v-if="$v.country.$dirty && !$v.country.minLength" >&#10094;</span>
				<span class="done" v-if="$v.country.$dirty && $v.country.required && $v.country.minLength">&#10003;</span>
			</div>
<!--********** district **********-->
			<div class="item">
				<label for="district" >Область:</label>
				<input type="text" id="district" name="district"
					pattern="[А-Яа-яёЁ\.0-9\-, ]{4,}"
					v-model.trim="district"
					@input="setDistrict($event.target.value)"
				/>
				<span class="error-title" v-if="$v.district.$dirty && !$v.district.minLength">Поле должно содержать минимум {{$v.district.$params.minLength.min}} буквы</span>
				<span class="error" v-if="$v.district.$dirty && !$v.district.minLength" >&#10094;</span>
				<span class="done" v-if="$v.district.$dirty && $v.district.required && $v.district.minLength">&#10003;</span>
			</div>
<!--********** city **********-->
			<div class="item">
				<label for="city" class="required">Город:</label>
				<input type="text" id="city" name="city"
					pattern="[А-Яа-яёЁ\.0-9\-, ]{4,}"
					required
					v-model.trim="city"
					@input="setCity($event.target.value)"
				/>
				<span class="error-title" v-if="$v.city.$dirty && !$v.city.minLength">Поле должно содержать минимум {{$v.city.$params.minLength.min}} буквы</span>
				<span class="error" v-if="($v.city.$dirty && !$v.city.minLength) || !$v.city.required" >&#10094;</span>
				<span class="done" v-if="$v.city.$dirty && $v.city.required && $v.city.minLength">&#10003;</span>
			</div>
<!--********** street **********-->
			<div class="item">
				<label for="street">Улица:</label>
				<input type="text" id="street" name="street"
					pattern="[А-Яа-яёЁ\.0-9\-, ]{4,}"
					v-model.trim="street"
					@input="setStreet($event.target.value)"
				/>
				<span class="error-title" v-if="$v.district.$dirty && !$v.district.minLength">Поле должно содержать минимум {{$v.district.$params.minLength.min}} буквы</span>
				<span class="error" v-if="$v.district.$dirty && !$v.district.minLength" >&#10094;</span>
				<span class="done" v-if="$v.district.$dirty && $v.district.required && $v.district.minLength">&#10003;</span>


				<span class="error-title" v-if="!$v.street.minLength" >Поле должно содержать минимум {{$v.street.$params.minLength.min}} буквы</span>
				<span class="error" v-if="!$v.street.minLength" >&#10094;</span>
				<span class="done" v-if="$v.street.minLength && $v.street.required" >&#10003;</span>
			</div>
<!--********** house **********-->
			<div class="item">
				<label for="house" >Дом:</label>
				<input type="text" id="house" name="house"
					pattern="[А-Яа-яёЁ\.0-9\-, ]{2,}"
					v-model.trim="house"
					@input="setHouse($event.target.value)"
				/>
				<span class="error-title" v-if="!$v.house.minLength" >Поле должно содержать минимум {{$v.house.$params.minLength.min}} буквы</span>
				<span class="error" v-if="!$v.house.minLength" >&#10094;</span>
				<span class="done" v-if="$v.house.minLength && $v.house.required" >&#10003;</span>
			</div>
		</div>
	</fieldset>
</template>

<script>
import { required, minLength, numeric } from 'vuelidate/lib/validators'

export default {
	data() {
		return {
			postIndex: '',
			country: '',
			district: '',
			city: '',
			street: '',
			house: '',

		}
	},
	validations: {
		postIndex: {
			minLength: minLength(6),
			required,
			numeric
		},
		country: {
			minLength: minLength(4),
			required
		},
		district: {
			minLength: minLength(4),
			required
		},
		city: {
			required,
			minLength: minLength(4)
		},
		street: {
			minLength: minLength(4),
			required
		},
		house: {
			minLength: minLength(2),
			required
		}
	},
	methods: {
		setPostIndex(value) {
			this.postIndex = value;
			this.$v.postIndex.$touch();
		},
		setCountry(value) {
			this.country = value;
			this.$v.country.$touch();
		},
		setDistrict(value) {
			this.district = value;
			this.$v.district.$touch();
		},
		setCity(value) {
			this.city = value;
			this.$v.city.$touch();
		},
		setStreet(value) {
			this.street = value;
			this.$v.street.$touch();
		},
		setHouse(value) {
			this.house = value;
			this.$v.house.$touch();
		},
	}
}
</script>
