<template>
	<div id="app">
		<h1>Добавление нового клиента</h1>
		<form action="POST"
			@submit.prevent="onSubmit"
			@reset="reset"
		>
			<AddInfo/>
			<hr>
			<AddAddress/>
			<hr>
			<AddDocument/>
			<hr>
			<div class="wrapp">
				<button class="btn" type="reset" >Сбросить</button>
				<button class="btn" type="submit">Зарегистрировать</button>
			</div>
		</form>
		<div class="wrapp2" v-if="this.popap" >
			<div class="popup">
				Новый клиент успешно создан
			</div>
		</div>
	</div>
</template>

<script>
import AddInfo from '@/components/AddInfo';
import AddAddress from '@/components/AddAddress';
import AddDocument from '@/components/AddDocument';
export default {
	data() {
		return {
			popap: false,
			submitStatus: null,
		}
	},
	name: 'App',
	components: {
		AddInfo, AddAddress, AddDocument
	},
	methods: {
		onSubmit() {
			this.popap = true;
			setTimeout(() => {
					this.popap = false;
			}, 1500);
			location.reload();
		},
		reset() {
			document.getElementsByTagName('form')[0].reset();
			location.reload();
		}
	}
}
</script>

